@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Playfair+Display:wght@600&family=Plus+Jakarta+Sans:wght@200;300;400;500;600;700;800&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;
@layer base {
  * {
    @apply dark:text-white;
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    @apply font-jakarta_sans dark:text-white;
  }
  h1 {
    @apply text-[36px] max-xl:leading-[1.33] xl:text-[64px] font-bold leading-[1.2];
  }
  h2 {
    @apply text-[32px] xl:text-[36px] font-bold leading-[1.33];
  }
  h3 {
    @apply text-[22px] xl:text-[24px] font-semibold;
  }
  h4 {
    @apply text-xl font-normal;
  }
  h5 {
    @apply text-lg font-normal;
  }
  h6 {
    @apply text-base font-normal;
  }
  p {
    @apply font-Inter text-base font-normal leading-[1.75] -tracking-[0.3px] dark:text-white;
  }
  em {
    @apply not-italic;
  }
  a {
    @apply inline-block font-Inter text-base font-normal leading-[1.75] -tracking-[0.3px] dark:text-white;
  }
}
@layer utilities {
  .container {
    @apply mx-auto max-1xl:px-5 1xl:max-w-[1290px];
  }
  .container-fluid {
    @apply w-full px-4 1xl:mx-auto;
  }
  .stop-color {
    stop-color: theme("colors.gray.200");
  }
  .stop-color-dark {
    stop-color: theme("colors.gray.darkGradient");
  }
  .badge {
    @apply text-sm inline-block border mb-2 px-3 py-1 rounded-full border-primary text-primary bg-primary/10 leading-6;
  }
  .badge-primary {
    @apply bg-primary dark:text-sm font-medium;
  }
}
:root {
  --marquee-width: 100%;
  --marquee-height: 350px;
  /* --marquee-elements: 12; */ /* defined with JavaScript */
  --marquee-elements-displayed: 6;
  --marquee-element-width: calc(var(--marquee-width) / var(--marquee-elements-displayed));
  --marquee-animation-duration: calc(var(--marquee-elements) * 3s);
}
@media (max-width: theme("screens.lg")) {
  :root {
    --marquee-elements-displayed: 4;
  }
}
@media (max-width: theme("screens.md")) {
  :root {
    --marquee-elements-displayed: 3;
  }
}
@media (max-width: theme("screens.sm")) {
  :root {
    --marquee-elements-displayed: 2;
  }
}

.marquee-items {
  width: var(--marquee-width);
  overflow: hidden;
  position: relative;
  margin: 0 auto;
}
.marquee-items .marquee-content {
  list-style: none;
  height: 100%;
  display: flex;
  animation: scrolling var(--marquee-animation-duration) linear infinite;
  animation-duration: 30s;
}
.marquee-items .marquee-content:hover {
  animation-play-state: paused;
}
.marquee-items .marquee-content .marquee-content-list {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: var(--marquee-element-width);
  @apply after:absolute after:right-0 after:top-1/2 after:-translate-y-1/2 after:w-[1px] after:h-[60px] after:content-[url("../images/clients/client-item-border.svg")] dark:after:content-[url("../images/clients/client-item-border-dark.svg")];
}

@keyframes scrolling {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(calc(-1 * var(--marquee-element-width) * var(--marquee-elements)));
  }
}
.tab-content > .tab-pane {
  display: none;
}
.tab-content > .tab-pane.tabActive {
  display: block;
}
.tab-content > .tabActive {
  display: block;
}

.animate-keep-bounce {
  animation: bounce-open 0.3s;
}

@keyframes bounce-open {
  0% {
    transform: scale(0.7);
  }
  45% {
    transform: scale(1.05);
  }
  80% {
    transform: scale(0.95);
  }
  to {
    transform: scale(1);
  }
}
@layer components {
  .btn {
    @apply text-base font-Inter font-medium leading-6 py-[13px] px-[30px] -tracking-[0.3px] text-white hover:text-white dark:dark:hover:rounded-[30px] relative z-0 before:-z-10 before:rounded-[30px] before:absolute before:bottom-0 before:left-0 before:h-full before:w-full before:origin-right before:scale-x-0 before:bg-borderColour-dark dark:before:bg-primary-200 before:transition-transform before:duration-500 before:content-[""] before:hover:origin-left before:hover:scale-x-100 overflow-hidden after:absolute after:w-full after:h-full after:rounded-[30px] after:bg-paragraph after:dark:bg-primary after:left-0 after:top-0 after:-z-20;
  }
  .btn-navbar {
    @apply after:dark:bg-dark-200 dark:text-white;
  }
}
.btn-outline {
  @apply text-base font-Inter leading-6 py-[13px] px-[30px] -tracking-[0.3px] duration-500 transition-all hover:duration-500 hover:transition-all rounded-[30px] font-medium overflow-hidden bg-white border-borderColour dark:hover:dark:bg-transparent border dark:border-[#313330] dark:text-white hover:text-white relative z-10 before:-z-10 before:rounded-[30px] before:absolute before:bottom-0 before:left-0 before:h-full before:w-full before:origin-right before:scale-x-0 before:bg-paragraph dark:before:bg-primary before:transition-transform before:duration-500 before:content-[""] before:hover:origin-left before:hover:scale-x-100;
}

.btn-sm {
  @apply px-6 py-2;
}

*::after,
*::before {
  @apply leading-none text-[0];
}

.fa-solid::after,
.fa-solid::before,
.fa-brands::after,
.fa-brands::before,
.fa-regular::after,
.fa-regular::before {
  @apply leading-[1] text-base;
}

body {
  overflow-x: hidden;
  max-width: 100vw;
}

html {
  @apply scroll-smooth;
}

.nav-sticky {
  @apply duration-500 transition-all top-0 backdrop-blur-2xl py-1.5;
}

.mobile-menu {
  @apply fixed right-0 top-0 z-[111111] flex h-screen w-full translate-x-full items-center px-10 backdrop-blur transition ease-in-out mx-auto bg-primary dark:bg-dark p-2.5 shadow-nav rounded-none;
}
.mobile-menu.open {
  @apply translate-x-0;
}
.mobile-menu .faq-body {
  @apply absolute w-full left-0 top-12 p-5 group-hover:scale-y-100 bg-white dark:bg-dark-200 group-hover:opacity-100 rounded-md [&>*:not(:last-child)]:border-b [&>*:not(:last-child)]:border-dashed [&>*:not(:last-child)]:border-borderColour dark:[&>*:not(:last-child)]:border-borderColour-dark [&>*:not(:first-child)]:mt-2.5 z-10;
}

span {
  @apply inline-block;
}

.active {
  @apply bg-white dark:bg-dark-200 border-borderColour dark:border-borderColour/10;
}

.section-tagline {
  @apply uppercase font-medium leading-[1.3] mb-3;
}

.faq-item:has(.open) {
  @apply shadow-nav;
}

.faq-body {
  transition: height 300ms ease;
  @apply transition-[height] duration-300 ease-in-out;
}

.faq-header .plus {
  @apply inline-block;
}
.faq-header .minus {
  @apply hidden;
}
.faq-header.open .plus {
  @apply hidden;
}
.faq-header.open .minus {
  @apply inline-block;
}

.faq-body.close {
  @apply hidden;
}

.faq-body.collapsing {
  @apply h-0 overflow-hidden;
}

.faq-body.open {
  @apply block;
}

.social-link li a:hover svg path {
  @apply fill-primary transition-all duration-300 ease-linear;
}
.social-link li svg path {
  @apply fill-paragraph dark:fill-gray-100 transition-all duration-300 ease-linear;
}

.swiper-pagination-bullet {
  @apply w-[9px] h-[9px] rounded-full border border-paragraph dark:border-primary bg-transparent opacity-100;
}
.swiper-pagination-bullet.swiper-pagination-bullet-active {
  @apply w-3.5 h-3.5 rounded-full border border-paragraph dark:border-primary bg-primary dark:bg-primary/25;
}

.swiper-pagination-bullets {
  @apply flex items-center mx-auto justify-center;
}

.f-button svg {
  @apply stroke-white;
}

.fancybox__container {
  @apply z-[1111111100];
}

.fancybox__carousel {
  @apply z-[111111110000];
}

#rect,
#rect-2,
#rect-3,
#rect-4,
#rect-5,
#rect-6 {
  opacity: 0;
}

.singlePage h2 {
  @apply mb-9;
}
.singlePage h3 {
  @apply mt-12 mb-5;
}
.singlePage p {
  @apply mb-12;
}
.singlePage ul {
  @apply mb-12;
}
.singlePage ul li {
  @apply dark:text-white font-semibold font-jakarta_sans relative pl-10;
}
.singlePage ul li:not(:last-child) {
  @apply mb-6;
}
.singlePage ul li:before {
  @apply w-7 h-7 dark:text-primary absolute max-md:top-0 md:top-1/2 left-0 md:-translate-y-1/2 content-[url("../images/checkmark.svg")] dark:content-[url("../images/checkmark-dark.svg")];
}
.singlePage div {
  @apply mb-16;
}

.comapny-bar circle {
  stroke-dasharray: 100px;
}

.integration-slider {
  height: 350px;
  display: flex;
  align-items: center;
  position: relative;
}
.integration-slider:after {
  content: "";
  width: 45%;
  height: 100%;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  background: linear-gradient(270deg, #ffffff 34%, rgba(255, 255, 255, 0) 90%);
  z-index: 1;
}
.integration-slider:before {
  content: "";
  width: 40%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background: linear-gradient(90deg, #ffffff 34%, rgba(255, 255, 255, 0) 90%);
  z-index: 2;
}
.integration-slider .swiper-wrapper {
  height: auto !important;
}
.integration-slider-shape {
  position: absolute;
  width: 170px;
  height: 170px;
  left: calc(50% - 85px);
  background: linear-gradient(180deg, rgba(177, 227, 70, 0.6) 0%, rgba(177, 227, 70, 0.3) 100%);
  backdrop-filter: blur(15px);
  border-radius: 100%;
}
.integration-slider-shape:after {
  content: "";
  position: absolute;
  width: 260px;
  height: 260px;
  top: calc(50% - 130px);
  left: calc(50% - 130px);
  background: linear-gradient(180deg, rgba(177, 227, 70, 0.2) 0%, rgba(177, 227, 70, 0.1) 100%);
  backdrop-filter: blur(15px);
  border-radius: 100%;
}
.integration-slider-shape:before {
  content: "";
  position: absolute;
  width: 350px;
  height: 350px;
  left: calc(50% - 175px);
  top: calc(50% - 175px);
  background: linear-gradient(180deg, rgba(177, 227, 70, 0.2) 0%, rgba(177, 227, 70, 0.1) 100%);
  backdrop-filter: blur(15px);
  border-radius: 100%;
}

.dark .integration-slider:after {
  background: linear-gradient(270deg, #191917 30.7%, rgba(25, 25, 23, 0) 100%);
}
.dark .integration-slider:before {
  background: linear-gradient(90deg, #191917 30.7%, rgba(25, 25, 23, 0) 100%);
}
.dark .integration-slider-shape {
  background: linear-gradient(180deg, rgba(177, 227, 70, 0.6) 0%, rgba(177, 227, 70, 0.3) 100%);
}
.dark .integration-slider-shape:after {
  background: linear-gradient(180deg, rgba(177, 227, 70, 0.2) 0%, rgba(177, 227, 70, 0.1) 100%);
}
.dark .integration-slider-shape:before {
  background: linear-gradient(180deg, rgba(177, 227, 70, 0.2) 0%, rgba(177, 227, 70, 0.1) 100%);
}

[data-tab-content] {
  display: none;
}

.tab-items-active[data-tab-content] {
  display: block;
}

.stack-cards {
  --stack-cards-gap: var(--space-sm);
}

.stack-cards {
  --stack-cards-gap: 50px;
}
.stack-cards--item {
  position: relative;
  position: sticky;
  top: 80px;
  transform-origin: center top;
